$count: 12;
$countStart: $count;
.flex {
  @include breakpointMaker(true) {
    @while $count > 0 {
      &-#{$count} {
        @include flex-12($count);
      }

      @if $countStart != $count {
        &-#{$count} {
          @at-root .gap > & {
            @include flex-12($count, $gap: $padding);
          }
        }

        &-#{$count} {
          @at-root .gap-2 > & {
            @include flex-12($count, $gap: $padding * 2);
          }
        }
      }
      $count: $count - 1;
    }
    // --------
  }
}
