//-----------convert text to var(--var-name)---------------------//
@function Var($colorName) {
  $colorName: var(--#{$colorName});
  @return $colorName;
}

@function Color($colorName) {
  @return map-get($colors, $colorName);
}

/*
  color: Var(gray-light) ;
  color: Color(gray-dark) ;
*/

//-----------converts numeric indexes to string keys---------------------//

@function index-to-key($map, $index) {
  $keys: map-keys($map);
  // A list of all keys in a $breakpoints map
  @return nth($keys, $index);
}

//-------------------pixel to rem----------------------------------//
@function rem($size) {
  @return $size / 16px * 1rem;
}
// example
//  font-size: rem(16px);  => 1rem
//-------------------pixel to em----------------------------------//
$browser-context: 16; // Default

@function em($pixels, $context: $browser-context) {
  @return #{$pixels/$context}em;
}

// font-size: em(9);

//-------------------remove units----------------------------------//

@function remove-unit($number) {
  @if type-of($number) == "number" and not unitless($number) {
    @return $number / ($number * 0 + 1);
  }
  @return $number;
}
// example
//  font-size: remove-unit(2.99em);  => 2.99

//-------------------replace string----------------------------------//
@function str-replace($string, $search, $replace: "") {
  $string: #{$string};
  $index: str-index($string, $search);

  @if $index {
    @return str-slice($string, 1, $index - 1) + $replace +
      str-replace(
        str-slice($string, $index + str-length($search)),
        $search,
        $replace
      );
  }

  @return $string;
}
