//-------------------erase first letter (. or #)---------------------------//
@function erase($string, $what) {
  $string: quote(#{$string});
  $what: quote($what);
  $index: str-index($string, $what);
  @if $index == 1 {
    $sliced: str-slice($string, $index + 1, str-length($string));
    @return unquote($sliced);
  } @else {
    @return unquote($string);
  }
}
//---------------------string split--------------------------------//
@function str-split($string, $separator) {
  $string: #{$string};
  // empty array/list
  $split-arr: ();
  // first index of separator in string
  $index: str-index($string, $separator);
  // loop through string
  @while $index != null {
    // get the substring from the first character to the separator
    $item: str-slice($string, 1, $index - 1);
    // push item to array
    $split-arr: append($split-arr, $item);
    // remove item and separator from string
    $string: str-slice($string, $index + 1);
    // find new index of separator
    $index: str-index($string, $separator);
  }
  // add the remaining string to list (the last item)
  $split-arr: append($split-arr, $string);

  @return $split-arr;
}

/*
  $names: str-split(&, ", "); list of names 
*/

//-------------------separateTwoSelectors----------------------------------//
@function separateTwoSelectors($which: 1, $this: &) {
  $string: quote(#{$this});
  $index: null;
  $index: str-index(#{$string}, ",");

  @if $index != null {
    $first: str-slice($string, 0, $index - 1);
    $second: str-slice($string, $index + 2, str-length($string));

    @if $which == 1 {
      @return $first;
    } @else {
      @return $second;
    }
  }
  @return $string;
}

//----------------------------------------------------*/
@function encodecolor($string) {
  @if type-of($string) == "color" {
    $hex: str-slice(ie-hex-str($string), 4);
    $string: unquote("#{$hex}");
  }
  $string: "#" + $string;
  $string: str-replace(#{$string}, "##", "#");

  @return to-lower-case(unquote($string));
}

//-------------------strip unit----------------------------------//
@function strip-unit($number) {
  @if type-of($number) == "number" and not unitless($number) {
    @return $number / ($number * 0 + 1);
  }
  @return $number;
}
//-------------------calc rule of three----------------------------------//
@function ruleofthree($num1, $num2, $num3) {
  $algo: ($num3 * $num2) / $num1;
  @return $algo;
}
