//-------------------badge----------------------------------//

.badge {
  // @include badge(red, white);

  position: absolute;
  display: block;
  top: 0;
  right: 0;

  text-align: center;
  font-size: 11px;
  border-radius: 30%;
  padding: 3px;
  min-width: 10px;
  min-height: min-content;
}

.bg-divided {
  @include bg-2-colors(
    90deg,
    rgba(237, 237, 237, 1) 50%,
    rgba(19, 19, 19, 1) 50%
  );
}

//---------------- gap between childs -------------------------//
//----- row -----//
.gap-row {
  @include gap-row($margin / 2);
  &-2 {
    @include gap-row($margin);
  }
}
//----- column -----//
.gap-col {
  @include gap-col($margin / 2);
  &-2 {
    @include gap-col($margin);
  }
}

