// hamburger lines ===

%hamburger-lines {
  border-style: solid;

  border-bottom-width: 0.16em;
  border-top-width: 0.16em;
  border-right-width: 0px;
  border-left-width: 0px;
}

.hamburger {
  border-color: Var(light);
  &::before {
    border-color: Var(light);
  }
}
.hamburger-white {
  border-color: Var(white);
  &::before {
    border-color: Var(white);
  }
}
.hamburger,
.hamburger-white {
  color: transparent;
  height: 1em;
  width: 1em;
  position: relative;
  font-size: 15px;
  // border-left: 0px solid Var(light);
  // border-top: 0.16em solid Var(light);
  // border-right: 0px solid Var(light);
  // border-bottom: 0.16em solid Var(light);
  display: inline-block;

  // box-sizing: content-box;
  @extend %hamburger-lines;

  &::before {
    top: 0;
    left: 0;
    width: 100%;
    height: 0.41em;
    content: "";
    // border-left: 0px solid Var(light);
    // border-top: 0px solid Var(light);
    // border-right: 0px solid Var(light);
    // border-bottom: 0.16em solid Var(light);
    border-style: solid;

    border-bottom-width: 0.16em;
    border-top-width: 0em;
    border-right-width: 0px;
    border-left-width: 0px;

    display: block;
    // transform: translateY(0.1em);
    // position: absolute;
  }
}
