//----------------------Display Banner-------------------------------//
.featureBanner {
  position: relative;
  margin: 20px;
}
.featureBanner:before {
  content: attr(data-content);
  position: absolute;
  top: 5px;
  left: -8px;
  padding-right: 10px;
  color: Var(gray-dark);
  font-weight: bold;
  height: 0px;
  border: 15px solid Var(orange);
  border-right-color: transparent;
  line-height: 0px;
  box-shadow: -0px 5px 5px -5px #000;
  z-index: 1;
}
.featureBanner:after {
  content: "";
  position: absolute;
  top: 35px;
  left: -8px;
  border: 4px solid Var(orange-dark);
  border-left-color: transparent;
  border-bottom-color: transparent;
}
//-------------------tooltip----------------------------------//
.tooltip {
  position: relative;
}

.tooltip[data-tooltip]:after {
  border-top: 8px solid var(gray-dark);
  border-top: 8px solid hsla(0, 0%, 0%, 0.85);
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  content: "";
  display: none;
  height: 0;
  width: 0;
  left: 25%;
  position: absolute;
}
.tooltip[data-tooltip]:before {
  background: var(gray-dark);
  background: hsla(0, 0%, 0%, 0.85);
  color: #f6f6f6;
  content: attr(data-tooltip);
  display: none;
  font-family: sans-serif;
  font-size: 14px;
  height: 32px;
  left: 0;
  line-height: 32px;
  padding: 0 15px;
  position: absolute;
  text-shadow: 0 1px 1px hsla(0, 0%, 0%, 1);
  white-space: nowrap;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -o-border-radius: 5px;
  border-radius: 5px;
}
.tooltip[data-tooltip]:hover:after {
  display: block;
  top: -9px;
}
.tooltip[data-tooltip]:hover:before {
  display: block;
  top: -41px;
}
.tooltip[data-tooltip]:active:after {
  top: -10px;
}
.tooltip[data-tooltip]:active:before {
  top: -42px;
}

//------------------slider input----------------------------------*/
%switch {
  position: relative;
  display: inline-block;
  // font-size: 20px;
  width: 3.5em;
  height: 1.6em;
  font-size: 15px;
}
.switch,
.switch-center {
  @extend %switch;
  &::after,
  &::before {
    content: "";
  }
  & input:checked + .slider,
  & input:checked + .slider-round {
    background-color: Var(success);
    // background: linear-gradient(
    //   291deg,
    //   rgba(51, 245, 108, 1) 0%,
    //   rgba(40, 167, 69, 1) 100%
    // );
  }

  & input:checked + .slider:before,
  & input:checked + .slider-round:before {
    -webkit-transform: translateX(1.9em);
    -ms-transform: translateX(1.9em);
    transform: translateX(1.9em);
  }
  & input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  & input:checked ~ .label-on {
    opacity: 1;
  }
  & input:checked ~ .label-off {
    opacity: 0;
  }
}
.switch-center {
  display: block;
  @include mx-(auto);
}

// ------
%slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  transition: background-color 0.4s;
  background-color: Var(danger);
}
%slider-before {
  position: absolute;
  content: "";
  background-color: white;
  -webkit-transition: transform 0.4s ease-out;
  transition: transform 0.4s ease-out;
  height: 1.3em;
  width: 1.3em;
  left: 0.15em;
  top: 0.15em;
}

.slider {
  @extend %slider;

  &-round {
    @extend %slider;
    border-radius: 1em;
    &:before {
      border-radius: 1em;
      @extend %slider-before;
    }
  }

  &:before {
    @extend %slider-before;
  }
}

// -----
.label {
  &-on,
  &-off {
    position: absolute;
    line-height: 100%;
    top: 0.52em;
    color: white;
    // transition: opacity 0.4s ease;
    font-size: 0.8em;
  }
  &-on {
    left: 10%;
    // transform: translate(0%, -50%);
    opacity: 0;
  }
  &-off {
    right: 10%;
    // transform: translate(0%, -50%);
  }
}

