  <label class="switch">
    <input type="checkbox">
    <span class="slider"></span>
    <span class="label-on">no</span>
    <span class="label-off">off</span>
  </label>
*/

//------------------input label move----------------------------------*/
.input {
  &::placeholder {
    // the input require to have a placeholder to work
    color: transparent;
  }

  &:not(:placeholder-shown) ~ label,
  &:focus ~ label {
    transform: translateY(-100%);
    font-size: $font-size * 0.7;
  }

  & + label {
    position: absolute;
    top: 0;
    left: 0;
    transition: 0.3s;
  }
}
