    // }
    // &.round {
    //   #{$name}-control:first-child {
    //     padding: 0 0.2em 0.2em 0.5em;
    //     border-radius: 5em 0 0 5em;
    //   }
    //   #{$name}-icon:last-child {
    //     border-radius: 0 5em 5em 0;
    //   }
    // }
  }
  // -----
  &-control {
    height: 1.7em;
    padding: 0 0.2em 0.2em 0.2em;
