}

// -----
.select {
  &-group {
    position: relative;
    z-index: 1;
    select {
      z-index: 2;
      border: none;
      background: inherit;
      color: inherit;
      appearance: none;
      padding: 0.5em $padding;
      font-size: 1em;
    }
    // &::after {
    //   top: 0;
    //   right: 0;
    //   width: 4em;
    //   height: 100%;
    //   @include bg-(dark);
    // }

    // &::after,
    &::before {
      content: "";
      position: absolute;
      pointer-events: none;
    }

    &::before {
      z-index: 9;
      width: 1em;
      height: 1em;
      right: 0;
      top: 60%;
      transform: translate(-50%, -50%);

      pointer-events: none;
      border-left: 0.6em solid transparent;
      border-right: 0.6em solid transparent;
      border-top: 0.6em solid rgba(255, 255, 255, 0.5);
    }
  }
}

