  white-space: nowrap;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -o-border-radius: 5px;
  border-radius: 5px;
}

.tooltip[data-tooltip]:hover:after {
  display: block;
  top: -9px;
}

.tooltip[data-tooltip]:hover:before {
  display: block;
  top: -41px;
}

.tooltip[data-tooltip]:active:after {
