.switch,
.switch-center {
  position: relative;
  display: inline-block;
  width: 3.5em;
  height: 1.6em;
  font-size: 15px;
}

.switch::after, .switch::before,
.switch-center::after,
.switch-center::before {
  content: "";
}
.switch input:checked + .slider, .switch input:checked + .slider-round,
.switch-center input:checked + .slider,
.switch-center input:checked + .slider-round {
  background-color: var(--success);
}
.switch input:checked + .slider:before, .switch input:checked + .slider-round:before,
.switch-center input:checked + .slider:before,
.switch-center input:checked + .slider-round:before {
  -webkit-transform: translateX(1.9em);
  -ms-transform: translateX(1.9em);
  transform: translateX(1.9em);
}
.switch input,
.switch-center input {
  opacity: 0;
  width: 0;
  height: 0;
}
.switch input:checked ~ .label-on,
.switch-center input:checked ~ .label-on {
  opacity: 1;
}
.switch input:checked ~ .label-off,
.switch-center input:checked ~ .label-off {
  opacity: 0;
}

.switch-center {
  display: block;
  margin-right: auto;
  margin-left: auto;
}

.slider-round, .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  transition: background-color 0.4s;
  background-color: var(--danger);
}

.slider:before, .slider-round:before {
  position: absolute;
  content: "";
  background-color: white;
  -webkit-transition: transform 0.4s ease-out;
  transition: transform 0.4s ease-out;
  height: 1.3em;
  width: 1.3em;
  left: 0.15em;
  top: 0.15em;
}

.slider-round {
  border-radius: 1em;
}
.slider-round:before {
  border-radius: 1em;
}
.label-on, .label-off {
  position: absolute;
  line-height: 100%;
  top: 0.52em;
  color: white;
  font-size: 0.8em;
}
.label-on {
  left: 10%;
  opacity: 0;
}
.label-off {
  right: 10%;
}

