  <label class="switch">
    <input type="checkbox">
    <span class="slider"></span>
    <span class="label-on">no</span>
    <span class="label-off">off</span>
  </label>
*/
.input::placeholder {
  color: transparent;
}
.input:not(:placeholder-shown) ~ label, .input:focus ~ label {
  transform: translateY(-100%);
  font-size: 1.19rem;
}
.input + label {
  position: absolute;
  top: 0;
  left: 0;
  transition: 0.3s;
}

