  position: absolute;
  font-size: 0.6em;
  background-color: #1fc8e3;
  background-color: var(--info-light);
  color: #fff;
  color: var(--light);
  right: 0.25em;
  cursor: pointer;
  top: 0.24em;
  padding: 0.7em;
  pointer-events: none;
}

