.flip_container {
  position: relative;
  perspective: 200px;
}
.flip_container .flip-vertical, .flip_container .flip-horizontal, .flip_container .flip-h, .flip_container .flip-v {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: absolute;
  transform-style: preserve-3d;
  transition: transform 0.5s ease;
}
.flip_container .front {
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.flip_container .flip-vertical .back, .flip_container .flip-v .back {
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transform: rotateX(180deg);
}
.flip_container .flip-horizontal .back, .flip_container .flip-h .back {
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transform: rotateY(180deg);
}
.flip_container:hover .flip-horizontal, .flip_container:hover .flip-h {
  transform: rotateY(180deg);
}
.flip_container:hover .flip-vertical, .flip_container:hover .flip-v {
  transform: rotateX(180deg);
}

.flip_click {
  position: relative;
  perspective: 200px;
}
.flip_click .flip-vertical, .flip_click .flip-horizontal, .flip_click .flip-h, .flip_click .flip-v {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  perspective: 200px;
  position: absolute;
  transform-style: preserve-3d;
  transition: transform 0.5s ease;
}
.flip_click .flip-vertical > *, .flip_click .flip-horizontal > *, .flip_click .flip-h > *, .flip_click .flip-v > * {
  overflow: hidden;
}
.flip_click .front {
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.flip_click .back-vertical, .flip_click .back-v {
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transform: rotateX(180deg);
}
.flip_click .back-horizontal, .flip_click .back-h {
  backface-visibility: hidden;
