//-------------------card----------------------------------//
.card {
  // @extend .d-inline-block;
  // width: 100%;
  // width: auto;
  // @include from($sm) {
  //   width: 400px;
  // }
  // background-color: white;

  height: fit-content;
  // height: min-content;
  overflow: hidden;
  border-collapse: collapse;

  // flex ------------------
  &-flex {
    @extend .card;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  // custom styles -----------------------
  &.stretch {
    height: auto;
  }
  & .head {
    // font-size: $font-size + 0.25;
    font-weight: 700;
    text-align: center;
  }
  & .body {
    // font-size: $font-size;
    // height: 100%;
    &-m {
      margin-top: $margin;
      margin-bottom: $margin;
    }
  }
  & .footer {
    // margin: auto ;
    font-size: $font-size - 0.25;
    align-self: flex-start;
    justify-self: flex-start;
    // font-style: italic;
    // background-color: red;
  }

  &-column {
    @extend .card;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  &-row {
    // width: 30%;
    @extend .card;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}

