//------------------card3----------------------------------*/

%card-img-float {
  display: block;
  height: auto;
}
.card-float {
  position: relative;
  clear: both;

  &-img {
    max-width: 200px;
    @extend %card-img-float;
    float: left;
    &-left,
    &-l {
      @extend %card-img-float;
      float: left;
    }
    &-right,
    &-r {
      @extend %card-img-float;
      float: right;
    }
  }

  &-body {
    display: block;
    clear: right;
  }
}

//------------------card4----------------------------------*/
.card-flex {
  @include flex-dcia(row, scpace-between);
  flex-wrap: wrap;
  &-img {
    flex: 0 0 auto;
    min-width: 200px;
  }
  &-body {
    flex: 1 1 auto;
    min-width: 200px;
  }
}
