// /* Removes awkward default styles on some inputs for iOS */

input[type="email"],
input[type="number"],
input[type="search"],
input[type="text"],
input[type="tel"],
input[type="url"],
input[type="password"],
input[type="range"],
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
textarea {
  min-height: $font-size * 3;
  min-width: 100%;
  max-width: 100%;
  // padding-top: 6px;
  // padding-bottom: 6px;
}
input:focus,
input[type="email"]:focus,
input[type="number"]:focus,
input[type="search"]:focus,
input[type="text"]:focus,
input[type="tel"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
textarea:focus,
select:focus {
  // border: 1px solid $input-line-focus;
  outline: none;
}

fieldset {
  padding: 0;
  border-width: 0;
}
//-------------------checkbox----------------------------------//
input[type="checkbox"],
input[type="radio"] {
  display: inline;
  margin: 0 $margin / 2;
}

//-------------------label----------------------------------//
label {
  display: inline-block;
  font-weight: $normal;
  cursor: pointer;
}

//-------------------range----------------------------------//
input[type="range"] {
  width: 100%;
}
%range {
  padding: 0;
  border-width: 1px;
  border-style: solid;
  border-color: $input-line;
}
.range-line {
  height: 1px;
  @include bg-(gray-light);

  // border-width: 0;
  // @extend %range;
  // position: relative;
  // z-index: 1;
  // @include before(absolute, "", block) {
  //   width: 100%;
  //   height: 50%;
  //   top: 0;
  //   left: 0;

  //   border-bottom: 2px solid Color(gray-light);
  // }

  &,
  &[type="range"]::-webkit-slider-runnable-track,
  &[type="range"]::-webkit-slider-thumb {
    border-radius: 0;
    -webkit-appearance: none;
  }
  &[type="range"]::-webkit-slider-thumb {
    @include bg-(primary);
    width: 20px;
    height: 20px;
    border-radius: 50%;
  }
}

.range-round {
  @extend %range;

  &,
  &[type="range"]::-webkit-slider-runnable-track,
  &[type="range"]::-webkit-slider-thumb {
    border-radius: 50px;
    -webkit-appearance: none;
  }
  &[type="range"]::-webkit-slider-thumb {
    // background-color: $primary;
    // background-color: Var(primary);
    @include bg-(primary);
    width: 20px;
    height: 20px;
    // border: 3px solid transparent;
    border-radius: 50%;
  }
}
.range {
  @extend %range;

  &,
  &[type="range"]::-webkit-slider-runnable-track,
  &[type="range"]::-webkit-slider-thumb {
    border-radius: 0;
    -webkit-appearance: none;
  }
  &[type="range"]::-webkit-slider-thumb {
    @include bg-(primary);
    width: 20px;
    height: 20px;
    border-radius: 0;
  }
}

