.animation {
  &-duration {
    animation-duration: 1s;
  }
  &-fill-mode {
    animation-fill-mode: forwards;
  }
  &-iteration-count {
    animation-iteration-count: infinite;
  }
}

$animation-fill-mode: forwards, backwards, both, none;
$animation-iteration-count: infinite;

.transition {
  &-duration {
    transition-duration: 1s;
  }
  &-delay {
    transition-delay: 1s;
  }
  &-delay {
    transition-delay: 1s;
  }
}

$transition-delay: 1s;

