//------------------border----------------------------------*/
.border {
  position: relative;
  &::before {
    content: "";
    position: absolute;
    width: 100%;
    z-index: -1;

    height: 0.2em;
    transition: transform 0.3s;
    transform: scaleX(0);

    background-color: red;
    left: 0;
    bottom: 0;
  }
  &:hover::before,
  &.active::before {
    transform: scaleX(1);
  }
}
