//-------------------Buttons----------------------------------//
.button,
.btn,
button,
input[type="submit"],
input[type="reset"],
input[type="button"] {
  background: none;
  // @include btn();
  padding: ($padding / 4) $padding / 2;
  // letter-spacing: 1;
  text-decoration: none;
  border: inherit;
  cursor: pointer;
  // display: block;
  outline: 0;
  // background-color: transparent;
  // font-size: $font-size;
  // font-family: inherit;

  &:hover,
  &:focus {
    outline: 0;
    // background-color: #0000006b;
    // color: initial;
    // opacity: 0.8;
  }

  &:active {
    outline: 0;
  }
}

.btn {
  // padding: 0.6em 0.8em 0.8em;

  // border-radius: 5px;
  // box-shadow: inset 0 -0.2em 8px -4px rgba(0, 0, 0, 0.2);
  // box-shadow: 0 0.2em 5px rgba(0, 0, 0, 0.2);
  // outline: none;
  display: inline-block;
  transition: 0.2s;
  &-left {
    @extend .btn;
    // margin-left: auto;
    margin-right: auto;
  }
  &-center {
    // display: block;
    @extend .btn;
    margin-left: auto;
    margin-right: auto;
  }
  &-right {
    // display: block;
    @extend .btn;
    margin-left: auto;
    // margin-right: auto;
  }
}

button,
.btn {
  @include noSelectText;
}

.btn-border {
  transition: outline-style 0.2s ease, outline-offset 0.2s ease;
  border: 2px solid $primary;
  // outline: 2px solid $primary;
  // &:hover {
  // outline-offset: 2px;
  // outline: 2px solid $primary;
  // }
}

$num: 1;
.btn {
  @each $key, $value in $breakpoints {
    & -#{$key} {
      @include prop(p(($padding * $num) / 4 $padding * $num / 2));
    }
    $num: $num + 0.5;
  }
}
